@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldregular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  src: url("../fonts/oswaldmedium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
/* Global */
:root {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.content-box-component {
  box-sizing: content-box;
}

html {
  height: 100%;
  font-family: Arial, sans-serif;
  font-size: 14px;
  line-height: 1.28;
}
@media (min-width: 768px) {
  html {
    font-size: 16px;
    line-height: 1.5;
  }
}

body {
  height: 100%;
}

button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
}

ul li,
ol li {
  list-style: none;
}

dd {
  margin: 0;
}

label {
  display: block;
  cursor: pointer;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.clearfix::after {
  content: "";
  display: table;
  clear: both;
}

.wrapper {
  position: relative;
  display: grid;
  grid-template-rows: 1fr min-content;
  min-height: 100%;
  min-width: 320px;
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 1220px;
  padding: 0 20px;
}
.container_p {
  padding: 0;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1370px) {
  .container {
    padding: 0;
  }
}

.no-js .wrapper::before {
  content: "Пожалуйста, разрешите использовать JavaScript! Без него сайт грустит...";
  position: fixed;
  top: -300px;
  left: 0;
  width: 100%;
  padding: 20px;
  z-index: 99999;
  text-transform: uppercase;
  text-align: center;
  font-size: 1.1rem;
  font-weight: 500;
  color: #f1535e;
  background-color: rgba(0, 0, 0, 0.75);
  animation-name: real;
  animation-delay: 3s;
  animation-duration: 7s;
  animation-fill-mode: forwards;
}

@keyframes real {
  0% {
    top: -300px;
  }
  10% {
    top: 0;
  }
  90% {
    top: 0;
  }
  100% {
    top: -300px;
  }
}
.title {
  margin: 0;
  font-family: "Oswald", Arial, sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #ffffff;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
    color: #000000;
  }
}

.subtitle {
  font-family: "Oswald", Arial, sans-serif;
  font-weight: 400;
  display: flex;
  align-items: center;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .subtitle {
    font-size: 36px;
    line-height: 36px;
  }
}

.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 54px;
  padding: 14px 26px 14px 26px;
  border: none;
  font-family: "Oswald", Arial, sans-serif;
  font-weight: 400;
  font-size: 20px;
  color: #ffffff;
  text-transform: uppercase;
  background-color: #68B738;
  transition: color 0.1s, background-color 0.1s;
}
.btn:hover {
  background-color: #5EAA2F;
}
.btn:active {
  color: rgba(255, 255, 255, 0.3);
}

.btn_show-all {
  background-color: #F2F2F2;
  color: #444444;
}
.btn_show-all:hover {
  background-color: rgba(242, 242, 242, 0.7);
}
.btn_show-all:active {
  color: rgba(0, 0, 0, 0.3);
}

.logo {
  display: inline-block;
  width: 190.4px;
  height: 37.6px;
  transition: opacity 0.1s;
}
.logo:hover {
  opacity: 0.8;
}
.logo:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .logo {
    width: 172.3px;
    height: 57.1px;
  }
}
@media (min-width: 1370px) {
  .logo {
    width: 202px;
    height: 60px;
  }
}
.logo .logo_footer {
  width: 127.8px;
  height: 23.4px;
}

.link-arrow {
  display: inline-flex;
  align-items: center;
  padding: 4px 0;
}
.link-arrow__text {
  font-family: "Oswald", Arial, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 768px) {
  .link-arrow__text {
    font-size: 20px;
    line-height: 30px;
  }
}
.link-arrow__arrow {
  position: relative;
  width: 24px;
  height: 12px;
  margin-left: 15px;
  transition: width 0.1s;
}
.link-arrow__arrow::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  width: calc(100% - 2px);
  transform: translateY(-50%);
  height: 2px;
  background: #000;
}
.link-arrow__arrow::after {
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 7px;
  height: 12px;
  margin-left: 28px;
  background-image: url("../img/triangle.svg");
  background-repeat: no-repeat;
  background-position: center;
}
.link-arrow:hover .link-arrow__arrow {
  width: 32px;
}
.link-arrow:active {
  opacity: 0.3;
}

.controls {
  width: 228px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 768px) {
  .controls {
    width: initial;
  }
}

.controls__btn {
  padding: 5px 15px;
  border: none;
  background-color: transparent;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .controls__btn {
    display: inline-flex;
    flex: 0 0 auto;
    height: 35px;
    margin-right: 25px;
    padding: 0;
    font-size: 20px;
    line-height: initial;
    border-bottom: 1px dotted #B2B2B2;
  }
  .controls__btn:last-child {
    margin-right: 0;
    margin-left: 25px;
  }
  .controls__btn:hover {
    border-bottom: 1px solid #919191;
  }
  .controls__btn:active {
    border: none;
  }
}

.conrols__line {
  position: relative;
  flex-grow: 1;
  height: 14px;
  padding: 3px 5px;
  border: 1px solid #CDCDCD;
  border-radius: 10px;
  background: #ffffff;
}
@media (min-width: 768px) {
  .conrols__line {
    height: 6px;
    width: 428px;
    border: none;
    padding: 0;
    background-color: #dcdcdc;
  }
}

.controls__handle {
  display: block;
  background-color: #68B738;
  height: 100%;
  border-radius: 10px;
}
@media (min-width: 768px) {
  .controls__handle {
    position: absolute;
    top: 50%;
    left: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    transform: translate(-50%, -50%);
    width: 34px;
    max-width: 34px;
    min-width: 34px;
    height: 34px;
    border-radius: 50%;
    background: #ffffff;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.16);
    cursor: pointer;
  }
  .controls__handle:after {
    content: "";
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #68B738;
  }
  .controls__handle:hover {
    box-shadow: 0px 3px 5px rgba(0, 0, 0, 0.16);
  }
  .controls__handle:active {
    border: 1px solid #68B738;
  }
}

